@font-face {
  font-family: "Open Sans";
  src: url("../../assets/fonts/OpenSans/OpenSans-Regular.ttf");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@font-face {
  font-family: "Open Sans";
  src: url("../../assets/fonts/OpenSans/OpenSans-SemiBold.ttf");
  font-weight: 600;
  font-style: normal;
  font-display: block;
}

@font-face {
  font-family: "Open Sans";
  src: url("../../assets/fonts/OpenSans/OpenSans-Bold.ttf");
  font-weight: 700;
  font-style: normal;
