@import "/src/theme/base/variables";

.slogan {
  position: relative;
  padding: 20px 15px 30px;
}

.slogan::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url(../../assets/img/slogan_bg_sm.png);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.slogan::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: -1;
}

.slogan .content {
  text-align: center;
}

.caption {
  margin-bottom: 21px;
  padding: 0 20px 0;
  font-size: 30px;
  line-height: 35px;
  color: $light-orange;
}

.text {
  margin-bottom: 41px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 700;
  color: $white;
  font-family: "Open Sans", sans-serif;
}

.slogan button {
  display: block;
  margin: 0 auto;
}

@media (min-width: 1440px) {
  .slogan {
    font-size: 52px;
    line-height: 60px;
    padding: 80px 186px 104px 165px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .slogan:before {
    background-image: url(../../assets/img/slogan_bg_lg.jpg);
  }

  .content {
    max-width: 601px;
  }

  .caption {
    margin-bottom: 20px;
    padding: 0;
    text-align: left;
    font-size: 52px;
    line-height: 60px;
  }

  .text {
    margin: 0;
    text-align: left;
  }

}
