@import './base/reset';
@import './base/typhoraphy';
@import './base/variables';

* {
  box-sizing: border-box;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  // background-color: indianred;
}

.btn {
  min-width: 139px;
  padding: 12px 9px;
  cursor: pointer;
  border: none;
  background-color: $orange;
  color: $white;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 19px;
  font-weight: 700;
  font-family: 'Open Sans', sans-serif;
  transform: 2s;
}

.btn:hover {
  transform: 2s;
  filter:invert(100%)
}

@media (min-width: 1024px){
  .container {
    max-width: 1024px;
  }

  .btn {
    min-width: 157px;
    padding: 14px 18px;
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1440px;
  } 
}