@import "/src/theme/base/_variables.scss";

.card {
  margin: 0 auto 14px;
  padding: 12px;
  max-width: 290px;
  border: 1px solid #000;
}

.card:last-child {
  margin: 0 auto;
}

.stack-sm>:nth-child(3)  {
  margin-top: 6px;
}

.stack-md>:nth-child(3) {
  margin-top: 30px;
  padding: 0 40px 0;
}

.card h2 {
  font-size: 26px;
  line-height: 26px;
  text-align: center;
  color: $dark;
}

.card h3 {
  text-align: center;
  font-size: 22px;
  line-height: 25px;
  color: $grey;
}

.card span {
  line-height: 29px;
  color: $gold;
}

.card .img {
  display: flex;
  justify-content: center;
}

.icon {
  width: 89px;
  height: 89px;
}

@media (min-width: 1024px) {

  .container {
    max-width: 1024px;
  }

  .cards {
    display: flex;
  }

  .card {
    margin: 0 auto;
    padding: 10px;
    width: 350px;
  }

  h2 {
    line-height: 29px;
  }

  h3 {
    font-size: 26px;
    line-height: 30px;
  }

  .stack-sm>:nth-child(3),
  .stack-md>:nth-child(3) {
    margin-top: 20px;
  }
}
