.images {
  width: 200px;
  height: 200px;
  position: relative;
}

.images::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  // background-image: url(../../assets/img/history_bg_sm_1.jpg);
  background-repeat: no-repeat;
}

.images::after {
  content: "";
  position: absolute;
  top: 20px;
  right: 0;
  bottom: 0;
  left: 0;
  // background-image: url(../../assets/img/history_bg_sm_2.jpg);
}